.intro {
    background: linear-gradient(black 0%, rgba(171, 80, 0, 0.708) 50%, black 100%);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    z-index: 1;
    // animation: fadeIn ($time-run + 1.6s) ease-in forwards;

    &__logo-img {
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0.5;
        width: clamp(250px, 100vw, 600px);
    }

    &__paragraph {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-shadow: none;
        position: absolute;
    }

    &__greeting {
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: 'Roboto';
        margin: 1vh;
        color: white;
        text-shadow: 1px 1px 2px black;
        font-size: clamp(26px, 8vw, 56px);
        // animation: fadeLeft ($time-run + 2.5s) ease-in-out forwards;

        &-2 {
            display: flex;
            justify-content: center;
            align-items: center;
            font-family: 'Roboto';
            line-height: 5vh;
            color: white;
            width: 80vw;
            max-width: 590px;
            font-size: clamp(14px, 4vw, 28px);
            text-align: center;
            text-shadow: 1px 1px 2px black;
            // animation: fadeRight ($time-run + 2.5s) ease-in-out forwards;
        }
    }
}