
.techstack-page__flexbox {
    
    display: flex;
    justify-content: center;
    align-items: center;
    width: 96%;
    height: 70%;


    // &-1-img {
    //     background: url(../img/calendar-api-pic-vert.png);
    // }

    // &-2-img {
    //     background: url(../img/calendar-api-pic-vert.png);
    // }

    // &-3-img {
    //     background: url(../img/calendar-api-pic-vert.png);
    // }

    // &-4-img {
    //     background: url(../img/calendar-api-pic-vert.png);
    // }

    // &-5-img {
    //     background: url(../img/calendar-api-pic-vert.png);
    // }

    // &-6-img {
    //     background: url(../img/calendar-api-pic-vert.png);

    // }

    &-1, &-2, &-3, &-4, &-5, &-6 {
        position: relative;
        overflow: hidden;
        height: 20vh;
    }

    &-1-img, &-2-img, &-3-img, &-4-img, &-5-img, &-6-img {
        position: absolute;
        height: 100%;
        width: 100%;
        background-size: cover;
        transform: scale(1);
        transition: all 0.5s;

        .demo-button {
            position: absolute;
            background: rgba(255, 132, 0, 0.8);
            width: 50%;
            height: 100%;
            left: 5%;
            display: flex;
            justify-content: center;
            align-items: center;
            justify-content: flex-end;
            transform: translateX(-120%);
            transition: all 0.5s;

            
            .example-button, .code-button {
                margin: 1vw 3vw 1vw 0;
                width: clamp(60px, 12vw, 90px);
                height: clamp(25px, 6vw, 40px);
                border-radius: clamp(10px, 2vw, 20px);
                font-size: clamp(10px, 2vw, 18px);
            }
        }

        &:before {
            position: absolute;
            content: '';
            bottom: 0;
            right: 0;
            width: 100%;
            height: 100%;
            background:rgba(0, 49, 63, 0.8);
        }
        

        &:hover {
            transform: scale(1.3);
            transition: all 0.5s forwards;

            .demo-button {
                transform: translateX(0);
            }
        }
    }

    &-text {
        position: absolute;
        color: white;
        font-size: clamp(1rem, 2.6vw, 2rem);
        padding-left: 5px;
        top: 30%;
        left: 0;

    }


    .techstack-flexbox {
        position: relative;
        width: clamp(100px, 15vw, 190px);
        height: clamp(200px, 35vw, 380px);
        margin: 2vw;

        &:before {
            position: absolute;
            content: '';
            bottom: 0;
            right: 0;
            width: 100%;
            height: 100%;
            background:rgba(0, 195, 255, 0.2);
        }

    }
}